import React, { useState, useEffect } from 'react'
import useApiOpenAI from '../../../../Shared/services/api/useApiOpenAI'

const OpenAIDescriptionBtn = (props) => {
  const { apiOpenAI } = useApiOpenAI()
  const { formValues, changeVal } = props
  const jobTitle = formValues && formValues.title ? formValues.title : ''
  const jobType = formValues && formValues.jobType ? formValues.jobType : ''
  const jobTypeData = jobType.replace(/_/g, " ");
  const jobStartDate = formValues && formValues.startDateAsap == true ? 'immediate' : ''
  const jobCommitment = formValues && formValues.commitment ? formValues.commitment : ''
  const jobCommitmentData = jobCommitment == 'over_30_hrs_weekly' ? 'Full Time' : jobCommitment == 'less_than_30_hrs_weekly' ? 'Part Time' : jobCommitment == 'i_dont_know_yet' ? 'Flexible' : ''
  const jobLocation = formValues && formValues.location.city
  const JobPreferredSkills = formValues && formValues.skills.map(skill => skill.label)
  const getPostJobData = {'title': jobTitle, 'Start Date': jobStartDate, 'Commitment': jobCommitmentData, 'location': jobLocation, 'skills': JobPreferredSkills}

  console.log('data....', getPostJobData)
  const [msg, setMsg] = useState('')
  const [loading, setLoading] = useState(false)
  const [generateDes, setGenerateDes] = useState(false)
  const [generateReq, setGenerateReq] = useState(false)

  const [textReq, setTextReq] = useState('')
  const [requirementText, setRequirementText] = useState('')
  const [currentIndexReq, setCurrentIndexReq] = useState(0)

  const [text, setText] = useState('')
  const [descriptionText, setDescriptionText] = useState('')
  const [currentIndex, setCurrentIndex] = useState(0)

  // console.log('fttttttttttttttttttttttttttt,', jobTypeData)

  // changeVal('description', text)
  // changeVal('requirements', textReq)

  const onRequirement = async () => {
    setLoading(true)
    setGenerateReq(true)
    const respRequirement = await apiOpenAI({
      messages: [
        {
          'role': 'system',
          'content': `Write a requirement to apply for the ${jobCommitmentData} ${jobTypeData} ${jobTitle}. ${jobLocation}. preferred skills: ${JobPreferredSkills}. Only provide requirements to apply in pointers in under 1800 characters. Do not add responsibilities and role description.`
        }],
      model: 'gpt-3.5-turbo'
    })
    // setRequirementText(respRequirement && respRequirement.choices && respRequirement.choices[0].message.content.replace(/(?:\r\n|\r|\n)/g, '<br />'))
    changeVal('requirements', respRequirement && respRequirement.choices && respRequirement.choices[0].message.content.replace(/(?:\r\n|\r|\n)/g, '<br />'))
    setLoading(false)
    setGenerateReq(false)
  }

  const handleSubmit = async (e) => {
    e.preventDefault()
    setLoading(true)
    setGenerateDes(true)
    if (jobTitle == 'undefined' || jobTitle == '' || jobTitle == null) {
      setMsg('Please enter the job title')
      // console.log('Please Enter the Job Title')
      setLoading(false)
      setTimeout(() => {
        setMsg('')
      }, 6000)
      return
    }
    if (jobTypeData == 'undefined' || jobTypeData == '' || jobTypeData == null) {
      setMsg('Please select the emploment type')
      setLoading(false)
      setTimeout(() => {
        setMsg('')
      }, 6000)
      return
    }
    if (jobCommitmentData == 'undefined' || jobCommitmentData == '' || jobCommitmentData == null) {
      setMsg('Please select the commitment')
      setLoading(false)
      setTimeout(() => {
        setMsg('')
      }, 6000)
      return
    }
    if (jobLocation == 'undefined' || jobLocation == '' || jobLocation == null) {
      setMsg('Please enter the location')
      setLoading(false)
      setTimeout(() => {
        setMsg('')
      }, 6000)
      return
    }
    if (JobPreferredSkills == 'undefined' || JobPreferredSkills == '' || JobPreferredSkills == null) {
      setMsg('Please select the preferred skills')
      setLoading(false)
      setTimeout(() => {
        setMsg('')
      }, 6000)
      return
    }
    const resp = await apiOpenAI({
      messages: [
        {
          'role': 'system',
          'content': `Write a job description for ${jobCommitmentData} ${jobTitle}. It is ${jobTypeData} job. Immidiate. ${jobLocation}. preferred skills: ${JobPreferredSkills}. (Based on it give Job description & role responsibilities in under 1800 characters) Do not add requirements to apply and benefits.`
        }],
      model: 'gpt-3.5-turbo'
    })
    onRequirement()
    // setDescriptionText(resp && resp.choices && resp.choices[0].message.content.replace(/(?:\r\n|\r|\n)/g, '<br />'))
    changeVal('description', resp && resp.choices && resp.choices[0].message.content.replace(/(?:\r\n|\r|\n)/g, '<br />'))
    // setLoading(false)
    setGenerateDes(false)
  }

  // useEffect(() => {
  //   const interval = setInterval(() => {
  //     if (currentIndex <= descriptionText.length) {
  //       setText((text) => {
  //         if (text.length < descriptionText.length) {
  //           return descriptionText.substring(0, currentIndex)
  //         } else {
  //           return text
  //         }
  //       })
  //       setCurrentIndex((currentIndex) => currentIndex + 5)
  //     } else {
  //       clearInterval(interval)
  //     }
  //   }, 100)

  //   return () => clearInterval(interval)
  // }, [descriptionText, currentIndex])

  useEffect(() => {
    const interval = setInterval(() => {
      if (currentIndex <= descriptionText.length) {
        setText(descriptionText => {
          // Use the functional form to ensure you're working with the latest state
          return descriptionText.substring(0, currentIndex)
        })
        setCurrentIndex(currentIndex => currentIndex + 5)
      } else {
        clearInterval(interval)
      }
    }, 100)

    return () => clearInterval(interval)
  }, [descriptionText, currentIndex])

  // useEffect(() => {
  //   const interval = setInterval(() => {
  //     if (currentIndex <= descriptionText.length) {
  //       setText(text => descriptionText.substring(0, currentIndex))
  //       setCurrentIndex(currentIndex + 5)
  //     } else {
  //       clearInterval(interval)
  //     }
  //   }, 0.5)

  //   return () => clearInterval(interval)
  // }, [text, descriptionText, currentIndex])

  useEffect(() => {
    const intervalR = setInterval(() => {
      if (currentIndexReq <= requirementText.length) {
        setTextReq(requirementText.substring(0, currentIndexReq))
        setCurrentIndexReq(currentIndexReq + 5)
      } else {
        clearInterval(intervalR)
      }
    }, 0.5)

    return () => clearInterval(intervalR)
  }, [textReq, requirementText, currentIndexReq])

  // changeVal('description', text)

  return (
    <div className={`olivia_button ${loading && 'animate_olivia_button'}`}>
      <button onClick={handleSubmit}>
        {!loading && <svg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'>
          <g clip-path='url(#clip0_2888_667)'>
            <path d='M13.7033 2.76604L13.787 2.98939L14.0133 3.06482L14.4189 3.2L14.0133 3.33518L13.787 3.41061L13.7033 3.63396L13.2 4.976L12.6967 3.63396L12.613 3.41061L12.3867 3.33518L11.9811 3.2L12.3867 3.06482L12.613 2.98939L12.6967 2.76604L13.2 1.424L13.7033 2.76604ZM12.1033 12.366L12.187 12.5894L12.4133 12.6648L12.8189 12.8L12.4133 12.9352L12.187 13.0106L12.1033 13.234L11.6 14.576L11.0967 13.234L11.013 13.0106L10.7867 12.9352L10.3811 12.8L10.7867 12.6648L11.013 12.5894L11.0967 12.366L11.6 11.024L12.1033 12.366ZM6.2967 6.15736L6.37856 6.38657L6.60945 6.46353L8.81886 7.2L6.60945 7.93647L6.37856 8.01344L6.2967 8.24264L5.2 11.3134L4.1033 8.24264L4.02144 8.01344L3.79055 7.93647L1.58114 7.2L3.79055 6.46353L4.02144 6.38657L4.1033 6.15736L5.2 3.08661L6.2967 6.15736Z' fill='white' stroke='#C8970F' />
          </g>
          <defs>
            <clipPath id='clip0_2888_667'>
              <rect width='16' height='16' fill='white' />
            </clipPath>
          </defs>
        </svg>}
        {loading ? <div>{(generateDes && 'Generating Description') || (generateReq && 'Generating Requirements')} <div class='dots' /></div> : 'Write with Olivia'}
        <span />
        <span />
        <span />
        <span />
        <span />
      </button>
      {msg && <p>{msg}</p>}
    </div>
  )
}

export default OpenAIDescriptionBtn



